@import url('https://fonts.googleapis.com/css2?family=Amatic+SC&family=Great+Vibes&family=Sacramento&display=swap');
@import url("https://use.typekit.net/bvr1mpa.css");

@tailwind base;
@tailwind components;
@tailwind utilities;


body {
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  background: rgb(252, 234, 234);
  padding:0;
  margin:0;
  font-family: 'Roslindale';
}

.prof {
  background-image: url('/src/assets/prof.jpg');
  background-size: contain;
}

.bigbag {
  background-image: url('/src/assets/bigbag.png');
  background-size: cover;
}


.vis {
  display: flex;
  height: 100%;
  transition: all .5s ease-in-out;
}
.hid{
  height: 0;
  transition: all .5s ease-in-out;
  opacity:0;
}


.textClip {
  background-clip: text;
  -webkit-background-clip: text;

}

.changeColor {
  animation: change ease-in ;

}



